.cb-oc-page-reputable {
	/* margin-top: -100px; */
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
	padding: 0px 15px 0;
	max-width: 1080px;
	background-color: #fff;

	position: relative;
	z-index: 10;
}


.cb-oc-page-reputable h2 {
	margin: 0;
	text-align: center;
	margin: 30px auto 20px;
	line-height: 1.2;
	color: #212529;
	font-size: 40px;
	font-weight: 700;
	/* font-family: "Poppins", sans-serif !important; */
}

.cb-oc-page-reputable p {
	/* font-family: "Poppins", sans-serif !important; */
	font-size: 24px;
	font-weight: 400;
	/* text-align: center; */
	line-height: 1.5;
	color: #212529;
text-align: left;
	margin: 0 0 20px;
	text-align: left;
}
.cb-oc-page-reputable p a{
text-decoration: none;
color: #02bdfc;
}
@media (min-width: 1024px) {
	.cb-oc-page-reputable {
		padding: 0px 30px 0;
	}
	.cb-oc-page-reputable h2 {
		font-size: 45px;
	}
	.cb-oc-page-reputable  p {
		font-size: 24px;
	}

}


